.fullVideoPage {
  color: white;
}

.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  border-top: 3px solid #FFF;
  border-right: 3px solid transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.channelnameandviews{
  display: flex;;
}
.large-screen-video-below-controls {
  display: none !important;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.full-video-page {
  position: relative;
  top: 59px;
}

.video_section {
  position: relative;
}

.middle_controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.middle_controls>div {
  display: flex;
  gap: 10px;
}

.middle_controls>div>svg {
  padding: 10px;
  font-size: 3rem;
  cursor: pointer;
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 24px;
  font-weight: bolder;
  z-index: 2;

}

.video_top_controls {
  color: white;
  display: none;
  justify-content: end;
  font-size: 1.4rem;
  position: absolute;
  right: 17px;
  gap: 4px;
  margin: 5px 10px 0px 0;
  font-weight: bolder;
}

.details {
  color: white;
  /* margin: 10px 10px 0 10px; */
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.show_views {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #aaa;
}

.below_controls {
  color: white;
  position: absolute;
  width: 100%;
  display: flex;
  line-height: 0.7;
  flex-direction: column;
  gap: 0.4rem;
  transform: translate(0%, -89%);
  top: 92%;

}

.below_controls>div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 10px;
}

.below_controls>div p {
  font-size: 0.7rem;
}

.channel_details>div>span {
  font-size: 0.8rem;
  margin-right: 0.6rem;
}

.comments_turnedOff_message {
  padding: 23px 20px 20px 14px;
}

.replybox button {
  background-color: white;
  color: black;
  padding: 7px 16px;
  float: right;
  border-radius: 16px;
  margin-top: 12px;
}

/*checking*/

.channel_details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 5px 5px 0 15px;
}

.channel_details_left_part {
  display: flex;
  align-items: center;
  flex-grow: 1;
  min-width: 0;
}

.channel_details_left_part img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

.channeName {
  flex-grow: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: bold;
  margin-right: 10px;
  /* Space between the text and subscribers */
}

.subscribe_btn {
  padding: 7px 10px;
  border: none;
  border-radius: 20px;
  background-color: white;
  cursor: pointer;
  color: black;
  font-size: 0.8rem;
  font-weight: 600;
  flex-shrink: 0;
  /* Prevents button from shrinking */
  margin-top: 5px;
}

.like_share_save_container {
  display: flex;
  gap: 0.6rem;
  margin-top: 8px;
  -webkit-box-pack: start;
  justify-content: start;
  overflow-x: auto;
  /* Use auto to allow scrolling */
  white-space: nowrap;
  margin-left: 12px;
  /* Hide scrollbar for webkit browsers */
  -webkit-scrollbar-width: none;
  scrollbar-width: none
}

.like_share_save_container>div {
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding: 0.1rem 1rem;
  border-radius: 21px;
  gap: 0.5rem;
}

.comments {
  background-color: rgba(255, 255, 255, 0.1);
  margin: 1em 0.8em;
  border-radius: 15px;
}

.comments_top {
  display: flex;
  align-items: center;
  font-size: 18px;
  gap: 17px;
  font-size: 1rem;
  /* background: #212121; */
  padding: 12px 12px 4px 16px;
  color: #f1f1f1;
}

#message_page {
  background-color: #0f0f0f;
  width: inherit;
  height: 100%;
  z-index: 2;
  overflow-y: scroll;
  color: white;
  position: fixed;
  border-radius: 14px 14px 0 0;
  width: 100%;
}

.message-top {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px 12px;
  background: #0f0f0f;
  position: -webkit-sticky;
  /* Added for Safari iOS support */
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  border-bottom: 0.1px solid white;
  color: white;
}

.message-body {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 2;
  overflow-y: scroll;
  color: white;
}

.message-top h3 {
  font-weight: 900;
  font-size: 1.1rem;
}

.send-message-input {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  padding: 10px 10px;
  gap: 0.5rem;
  align-items: center;
}

.send-message-input img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.send-message-input input {
  width: 100%;
  height: 4rem;
  color: #aaa;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 3px;
  background: transparent;
  border-radius: 12px;
}

#comment {
  display: flex;
  margin: 1.5em 0em
}

.commentChannelPic {
  display: flex;
  width: 56px;
  justify-content: center;
}

.commentChannelPic img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  padding: 0 0.5em;
}

.comment-content {
  padding: 0 0.5em;
}

.comment-right-section {
  font-size: 0.9em;
  width: 100%;
}

.comment-bottom {
  display: flex;
  justify-content: space-between;
  padding: 0.5em 0.5em 0 0.5em;
}

.comment-bottom .icons {
  display: flex;
  gap: 0.7em;
  align-items: center;
}

.comment-bottom .icons svg {
  font-size: 1.1rem;
}

.comment-bottom .icons span {
  color: rgb(170, 170, 170);
  font-size: 13.1px;
}

.like_share_save_container>div>svg {
  font-size: 1rem;
}

.likes,
.share,
.save,
.report {
  font-size: 0.8rem;
}

.Next_videos {
  border-top: 1px solid white;
}

.Next_Video>a>img {
  width: 100%;
}

.comment {
  display: flex;
  align-items: center;
  padding: 2px 0 10px 10px;
  gap: 0.5rem;
}

.comment img {
  border-radius: 25px;
  width: 28px;
}

.Next_Videos {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
}

.video_bottom {
  display: flex;
  justify-content: space-between;
  margin: 6px 7px 10px 7px;
  height: 47px;
  color: white;
  align-items: center;
}

.video_bttom_left img {
  width: 30px;
  border-radius: 20px;
}

.menu {
  font-size: 23px;
}

#video-title {
  font-family: "Roboto", "Arial", sans-serif;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  font-size: 1em;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  /* Added for compatibility */
  overflow: hidden;
  line-height: 1.25;
  text-overflow: ellipsis;
  font-weight: 800;
  font-size: 1.1em;
  margin: 10px 0 0px 0;
}

.subscribed_btn {
  background-color: #2f2d2b;
  color: white;
}

.like_share_save_container>div :hover {
  cursor: pointer;
}

.middle_controls,
.below_controls {
  display: none;
}

#fullscreenDiv {
  position: absolute;
  top: 0;
  margin: 24px;
  z-index: 5;
}

.fullscreenVideoTitle {
  font-size: 1.5rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  overflow: hidden;
  line-height: 1.25;
  text-overflow: ellipsis;
}

#description_page {
  background-color: #0f0f0f;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 2;
  border-radius: 14px 14px 0 0;
  overflow-y: scroll;
  color: white;
}

.description_top {
  display: flex;
  justify-content: space-between;
  padding: 12px 12px;
