  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
}

.creator-channel {
  display: flex;
  align-items: center;
}

.creator-channel img {
  width: 26px;
  height: 26px;
  border-radius: 50%;
}

.description_body {
  padding: 0.5rem 1rem;
  height: -webkit-fill-available;
  overflow-y: scroll;
}

.video-title h2 {
  padding: 4px 0;
