    width: 100%;
    height: 4px;
    accent-color: #ff0000;
  }
}

@media screen and (min-width:990px) and (max-width:1115px) {
  .fullVideoPage {
    margin-left: 10px;
  }
}

@media screen and (min-width:1745px) {
  #currentVideo {
    border-radius: 18px;
  }

  .large-screen-video-below-controls {
    transform: translate(0px, -91px);
  }

  .Next_videos .videos #video {
    max-width: calc(90% - 16px);
  }

  .Next_videos {
    /*top: 50px; */
    left: 62px;
  }
}

@media screen and (min-width:2000px) {
  .greaterthan-2000px-screen-div {
    height: 100vh;
    position: relative;
    top: 50px;
    width: 76%;
    margin: 0 auto;
  }

  .Next_videos {
    left: 12px;
  }
}